.button {
  display: inline-block;
  padding: 14px 27px;
  border-radius: 3px;
  background-color: $color-basic-cyan;
  font-weight: 600;
  text-align: center;
  color: $color-default-white;
  transition: $trans-default;

  @include hover-focus {
    background-color: $color-transparent;
    outline: 1.5px solid $color-basic-cyan;
    color: $color-basic-cyan;
  }

  @include vp-1280 {
    padding: 8px 40px;
  }

  @include vp-768 {
    padding: 12px;
    font-size: 14px;
    line-height: 16px;
    width: 100%;
  }
}
